@import "./reset.scss";
@import url("./fonts/fonts.css");
@import "./documentationPage.scss";
@import "./animation.scss";

$font-poster: 'TRY Vesterbro';
$font-monotype: 'IBM Plex Mono', monospace;
$font-docu-pages: 'GTAmericaMono-Regular';
$primary-color: #222222;
$secondary-color: #f4f1d0;
$tertiary-color: #c6e5df;
$red-color: #c33149;



body {
  font-family: $font-monotype;
}

//mobile-portrait
@media (min-width: 320px) and (max-width: 480px) {
  
  .container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;

    //OVERLAY MENU
    /* Nav Button */
    .navMenu {
      position: absolute;
      width: 40px;
      height: 40px;
      top: 15px;
      right: 15px;
      font-size: 40px;
      font-weight: 700;
      cursor: pointer;
    }

    /* Position the close button (top right corner) */
    .closebtn {
        position: absolute;
        top: 30px;
        right: 30px;
        font-size: 60px !important; /* Override the font-size specified earlier (36px) for all navigation links */
    }
    //

    .navbar {
      grid-column: span 2;
      padding: 1rem;
      //text-align: center;
      font-size: 1.4em;
      background-color: $primary-color;
      color: $secondary-color;
    
      &__logo--image {
        float: left;
      }

      &__icons {
        display: block;

              /* The Overlay (background) */
          .overlay {
            /* Height & width depends on how you want to reveal the overlay (see JS below) */    
            height: 0%;
            width: 100%;
            position: fixed; /* Stay in place */
            z-index: 999999; /* Sit on top */
            left: 0;
            top: 0;
            //background-color: rgb(51,51,51); /* Black fallback color */
            //background-color: rgba(0,0,0, 0.9); /* Black w/opacity */
            background-image: linear-gradient(to bottom, #a18cd1 0%, #fbc2ebf6 100%);
            overflow-y: hidden;
            transition: 0.5s;

            /* Position the content inside the overlay */
            &-content {
              position: relative;
              top: 25%;
              width: 100%;
              text-align: center; /* Centered text/links */
              margin-top: 30px; /* 30px top margin to avoid conflict with the close button on smaller screens */

              .navIcons {
                background-color: transparent;
                //text-align: center;
                font-size: .9em;
                letter-spacing: 0.1em;
                position: relative;
                //display: inline-block; 
                width: 100%;
                float: right;
                text-decoration: none;
                color: $secondary-color;
                padding: .8em;
                line-height: 0.1em;
              
                &:visited {
                  text-decoration: none;
                }
              
                &:hover {
                  font-weight: 700;
                  transform: translateY(-0.1rem);
                  text-shadow: 0 1rem 2rem rgba(244, 241, 208, 0.445);
                }
              
                &:active {
                  font-weight: 700;
                  transform: translateY(-0.1rem);
                  text-shadow: 0 0.5rem 1rem rgba(244, 241, 208, 0.493);
                }
              }
          }

            /* The navigation links inside the overlay */
            & a {
              padding: 8px;
              text-decoration: none;
              font-size: 36px;
              color: $secondary-color !important;
              display: block; /* Display block instead of inline */
              transition: 0.2s; /* Transition effects on hover (color) */
              //text-transform: uppercase;
              //font-family: 'Open Sans', sans-serif;

              /* When you mouse over the navigation links, change their color */
              &:hover, &:focus {
                font-weight: 700;
                transform: translateY(-0.1rem);
                text-shadow: 0 1rem 2rem rgba(244, 241, 208, 0.445);
              }
          }
        }
      }

    }

    nav {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      grid-template-rows: auto; 
      transition: width 350ms ease;
      width: auto;
      z-index: 9;
      justify-items: center;
      //height: auto;
      background: $tertiary-color;
      color: $primary-color;
      padding: 1rem;
      position: relative;
      //padding-top: 9vh;

      .style-selector-first {
        grid-column: 1;
        position: relative;
        //background-image: url(images/one.png);
        background-repeat: no-repeat;
        background-size: contain;
        height: 8vh;
        width: 8vh;
        left: 0rem;

        &:hover {
          animation: shake 1s cubic-bezier(.36,.07,.19,.97) both;
          transform: translate3d(0, 0, 0);
          backface-visibility: hidden;
          perspective: 1000px;
        }
      }
      
      .style-selector-second {
        grid-column: 2;
        position: relative;
        //background-image: url(images/two.png);
        background-repeat: no-repeat;
        background-size: contain;
        height: 8vh;
        width: 8vh;
        left: 0rem;

        &:hover {
          animation: shake 1s cubic-bezier(.36,.07,.19,.97) both;
          transform: translate3d(0, 0, 0);
          backface-visibility: hidden;
          perspective: 1000px;
        }
      }
      
      .style-selector-third {
        grid-column: 3;
        position: relative;
        //background-image: url(images/three.png);
        background-repeat: no-repeat;
        background-size: contain;
        height: 8vh;
        width: 8vh;
        left: 0rem;

        &:hover {
          animation: shake 1s cubic-bezier(.36,.07,.19,.97) both;
          transform: translate3d(0, 0, 0);
          backface-visibility: hidden;
          perspective: 1000px;
        }
      }
      
      .style-selector-fourth {
        grid-column: 4;
        position: relative;
        background-image: url(images/four.png);
        background-repeat: no-repeat;
        background-size: contain;
        height: 8vh;
        width: 8vh;
        left: 0rem;

        &:hover {
          animation: shake 1s cubic-bezier(.36,.07,.19,.97) both;
          transform: translate3d(0, 0, 0);
          backface-visibility: hidden;
          perspective: 1000px;
        }
      }
      
      .style-selector-fifth {
        grid-column: 5;
        position: relative;
        background-image: url(images/five.png);
        background-repeat: no-repeat;
        background-size: contain;
        height: 8vh;
        width: 8vh;
        left: 0rem;

        &:hover {
          animation: shake 1s cubic-bezier(.36,.07,.19,.97) both;
          transform: translate3d(0, 0, 0);
          backface-visibility: hidden;
          perspective: 1000px;
        }
      }
      
      .style-selector-sixth {
        grid-column: 6;
        position: relative;
        background-image: url(images/six.png);
        background-repeat: no-repeat;
        background-size: contain;
        height: 8vh;
        width: 8vh;
        left: 0rem;

        &:hover {
          animation: shake 1s cubic-bezier(.36,.07,.19,.97) both;
          transform: translate3d(0, 0, 0);
          backface-visibility: hidden;
          perspective: 1000px;
        }
      }
      
      .nav-list {
      
        grid-column: 1 / -1;

        & .placeholder {

          margin: 20px;

          & > li {
              color: $primary-color;
              text-transform: uppercase;
              padding: 3px;
              cursor: pointer;

              &:hover {
                font-weight: 700;
                transform: translateY(-0.1rem);
                text-shadow: 0 1rem 2rem rgba(244, 241, 208, 0.445);
              }
            }
          }
        }
    }

    .jumbo {
      padding: 20px;
    
      .text-box {
        background-size: 100%;
        position: absolute; 
        grid-column-start: 1;
        grid-column-end: span col4-start;
        z-index: 10;
      
        .heading {
          font-size: 3.2rem;
          font-family: 'TRY Vesterbro';
          font-weight: normal;
          font-style: normal;
          padding-top: 5vh;
          letter-spacing: 0em;
          line-height: .8em;
        
          &__first {
            display: block;
            overflow: hidden; // Ensures the content is not revealed until the animation 
            border-right: .15em solid transparent; // The typwriter cursor 
            white-space: nowrap; // Keeps the content on a single line 
            margin: 0 auto; // Gives that scrolling effect as the typing happens 
            animation: 
              typing 2s steps(40, end),
              blink-caret .75s step-end infinite;
            animation-duration: .8s;
          }
          
          &__second {
            height: 10vh;
            display: block;
            margin-left: 1rem;
            overflow: hidden; // Ensures the content is not revealed until the animation 
            border-right: .15em solid transparent; // The typwriter cursor
            //margin: 0 5rem; // Gives that scrolling effect as the typing happens 
            animation: 
              hideing .8s linear,
              typing 2s .75s steps(40, end) ,
              blink-caret .75s .75s step-end infinite;
            animation-duration: .9s;
          }
        }
      
        .heading__btn {
          position: relative;
          color: transparent;
          background: transparent;
          font-family: IBM Plex Sans;
          font-size: 0.9rem;
          //margin-top: 8rem;
          margin-left: 6rem;
          padding: 1rem 1rem;
          border-radius: 0.1rem;
          animation: moveInBottom .55s ease-out;
          transition: all .2s;
          &::after {
            content: "";
            display: inline-block;
            position: absolute;
            height: 100%;
            width: 100%;
            //background-color: $secondary-color;
            border: 0.09rem solid $primary-color;
            border-radius: .1rem;
            top: .35rem;
            left: -.35rem;
            z-index: -2;
            box-shadow: 0 0 0 1px $primary-color;
            transition: transform 150ms ease-out, -webkit-transform 150ms ease-out;
          }
          &::before {
            content: "Start the tour";
            display: inline-block;
            position: absolute;
            font-family: IBM Plex Sans;
            text-align: center;
            font-size: .95em;
            padding: 1rem 1rem;
            color: $secondary-color;
            z-index: -1;
            height: 100%;
            width: 100%;
            background-color: $primary-color;
            border-radius: .1rem;
            top: 0rem;
            left: 0rem;
          }
          &:hover::before{
            transform: translate(-.18rem, .18rem);
          }
          &:active::before {
            transform: translate(-.35rem, .35rem);
            }
        }
      }
    
      &__img--container {
        grid-area: 2 2;
        background-position-x: left;
        background-position-y: bottom;
    
        .jumbo__img--1 {
          background-image: url('./images/rinpa_duo.jpg');
          position: absolute;
          z-index: 3;
          height: 30vh;
          width: 30vh;
          background-size: cover;
          border-radius: 50%;
          top: 54%;
          right: 25%;
          background-position: bottom;
    
        }
        
        .jumbo__img--2 {
          z-index: 2;
          background-image: url('./images/jan_duo.jpg');
          background-position: center;
          background-size: 35rem;
          position: absolute;
          height: 45vh;
          width: 30vh;
          top: 34%;
          right: 46%;
          background-position: bottom;
        }
        
        .jumbo__img--3 {
          z-index: 1;
          background-image: url('./images/munari_duo.jpg');
          position: absolute;
          height: 30vh;
          width: 30vh;
          top: 33%;
          right: 18%;
          background-position: center;
          background-size: 23rem;
          clip-path: polygon(75% 0, 0 69%, 100% 100%);
          background-position: bottom;

        }
        
      }
      
      
    }

    footer {
      grid-column: span 2;
      padding: 30px;
      background-color: $primary-color;
      color: $secondary-color;
      font-size: 0.8rem;
      text-align: center;
      line-height: 1.2rem;
      //letter-spacing: .1em;
    }
  }

}

