            border-radius: .1rem;
            top: .35rem;
            left: -.35rem;
            z-index: -2;
            box-shadow: 0 0 0 1px $primary-color;
            transition: transform 150ms ease-out, -webkit-transform 150ms ease-out;
          }
          &::before {
            content: "Start the tour";
            display: inline-block;
            position: absolute;
            font-family: IBM Plex Sans;
            text-align: center;
            font-size: .95em;
            padding: 1rem 1rem;
            color: $secondary-color;
            z-index: -1;
            height: 100%;
            width: 100%;
            background-color: $primary-color;
            border-radius: .1rem;
            top: 0rem;
            left: 0rem;
          }
          &:hover::before{
            transform: translate(-.18rem, .18rem);
          }
          &:active::before {
            transform: translate(-.35rem, .35rem);
            }
        }
      }
    
      &__img--container {
